body{margin:0;padding:0;box-sizing:border-box;font-family:"Open Sans",sans-serif}.header{min-height:12vh;display:flex;width:100%;justify-content:center;align-items:center;position:-webkit-sticky;position:sticky;z-index:5;background-color:#fff;top:0;box-shadow:0px 15px 10px -20px gray}.header .logo{color:#377dff;font-size:30px;font-weight:bold}.header .toogle-btn{display:none}.header ul li{list-style-type:none;display:inline;margin-left:30px}.header ul li a{text-decoration:none;color:gray;font-size:1.2rem}.header ul li a:hover{color:#5d96ff}.header ul li a .def-btn{background-color:#377dff;color:#fff}.header ul li a .def-btn:hover{background-color:#3a8cf7}.motivation .text{padding:80px}.motivation .text .def-btn{margin-right:20px;background-color:#377dff;color:#fff;padding:10px}.motivation .text .def-btn:hover{background-color:#3a8cf7}.motivation .text .learnmore-btn{color:#377dff;padding:10px;font-weight:bold;background-color:#fff}.motivation .text h1{font-size:3.5rem}.motivation .image{background-image:url(/codecoffee/static/media/hero.0dc0b487.png);background-size:100% 100%;height:75vh}.cardheading{margin-top:30px}.cardheading h1{text-align:center;font-size:33px}.card{cursor:pointer;margin-top:25px;border-radius:10px;color:#fff;padding:25px}.card:hover{-webkit-transform:translateY(-8px);transform:translateY(-8px);transition:.3s}.card .name{font-size:1.6rem}.card .detail{margin-top:5px;font-size:1.1rem}.card .learnmore-btn{color:#fff;padding:10px;width:45%;font-weight:bold}.card img{padding:15px}.review{margin-top:80px;border-radius:15px;background-color:#f7faff;padding:50px}.review .client-img img{border-radius:15px}.review .client-comment{padding:30px;font-size:1.3rem}.review .client-comment .quote{color:#377dff;font-size:3rem}.review .case-studies{padding:25px}.review .case-studies h1{color:#377dff}.review .case-studies p{color:gray}.review .case-studies a{text-decoration:none}.benefits{margin-top:50px}.benefits .img{background-image:url(/codecoffee/static/media